import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, FormSection, formValueSelector, reduxForm } from "redux-form";
import { createNumberMask } from "redux-form-input-masks";
import { getListBarang } from "../actions/kodeBarangAction";
import { renderField, renderFieldSelect } from "../renderField";

const currencyMask = createNumberMask({
  prefix: "Rp. ",
  locale: "id-ID",
});
class CetakBarcodeHead extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }

  componentDidMount() {
    this.props.dispatch(getListBarang());
  }
  render() {
    return (
      <form onSubmit={this.props.handleSubmit}>
        <div className="row">
          <div className="col-lg-6">
            <h3>Barcode 1</h3>
            <hr />
            <div className="row">
              <div className="col-lg-6">
                <Field
                  name="kode_barang"
                  component={renderFieldSelect}
                  options={[]}
                  type="text"
                  label="Kode Barang"
                  placeholder="Masukan Kode Barang"
                />
              </div>
              <div className="col-lg-4">
                <Field
                  name="sub_kategori"
                  component={renderField}
                  type="text"
                  label="Sub Kategori"
                  placeholder="Masukan Sub Kategori"
                />
              </div>
              <div className="col-lg-3">
                <Field
                  name="pkg"
                  component={renderField}
                  type="text"
                  label="PKG (Gr)"
                  placeholder="Masukan Pkg"
                  onChange={() => this.props.change("gw", this.props.gw)}
                  onBlur={() => this.props.change("gw", this.props.gw)}
                />
              </div>
              <div className="col-lg-3">
                <Field
                  name="nw"
                  component={renderField}
                  type="text"
                  label="NW (Gr)"
                  placeholder="Masukan NW"
                  onChange={() => this.props.change("gw", this.props.gw)}
                  onBlur={() => this.props.change("gw", this.props.gw)}
                />
              </div>
              <div className="col-lg-3">
                <Field
                  name="gw"
                  component={renderField}
                  type="text"
                  label="GW (Gr)"
                  placeholder="Masukan GW"
                />
              </div>
              <div className="col-lg-3">
                <Field
                  name="tukaran"
                  component={renderField}
                  type="text"
                  label="Tukaran (%)"
                  placeholder="Masukan Tukaran (%)"
                />
              </div>
              <div className="col-lg-5">
                <Field
                  name="ongkos"
                  component={renderField}
                  type="telp"
                  label="Ongkos (Rp)"
                  placeholder="Masukan Ongkos (Rp)"
                  {...currencyMask}
                />
              </div>
            </div>
          </div>
          <div className="col-lg-6">
            <h3>Barcode 2</h3>
            <hr />
            <div className="row">
              <div className="col-lg-6">
                <Field
                  name="kode_barang2"
                  component={renderFieldSelect}
                  options={[]}
                  type="text"
                  label="Kode Barang"
                  placeholder="Masukan Kode Barang"
                />
              </div>
              <div className="col-lg-4">
                <Field
                  name="sub_kategori2"
                  component={renderField}
                  type="text"
                  label="Sub Kategori"
                  placeholder="Masukan Sub Kategori"
                />
              </div>
              <div className="col-lg-3">
                <Field
                  name="pkg2"
                  component={renderField}
                  type="text"
                  label="PKG (Gr)"
                  placeholder="Masukan Pkg"
                  onChange={() => this.props.change("gw2", this.props.gw2)}
                  onBlur={() => this.props.change("gw2", this.props.gw2)}
                />
              </div>
              <div className="col-lg-3">
                <Field
                  name="nw2"
                  component={renderField}
                  type="text"
                  label="NW (Gr)"
                  placeholder="Masukan NW"
                  onChange={() => this.props.change("gw2", this.props.gw2)}
                  onBlur={() => this.props.change("gw2", this.props.gw2)}
                />
              </div>
              <div className="col-lg-3">
                <Field
                  name="gw2"
                  component={renderField}
                  type="text"
                  label="GW (Gr)"
                  placeholder="Masukan GW"
                />
              </div>
              <div className="col-lg-3">
                <Field
                  name="tukaran2"
                  component={renderField}
                  type="text"
                  label="Tukaran (%)"
                  placeholder="Masukan Tukaran (%)"
                />
              </div>
              <div className="col-lg-5">
                <Field
                  name="ongkos2"
                  component={renderField}
                  type="telp"
                  label="Ongkos (Rp)"
                  placeholder="Masukan Ongkos (Rp)"
                  {...currencyMask}
                />
              </div>
            </div>
          </div>
        </div>
        <div className="col-lg-12">
          <div className="text-right">
            <button className="btn btn-primary">
              <i className="fa fa-print mr-2"></i> Cetak
            </button>
          </div>
        </div>
      </form>
    );
  }
}

CetakBarcodeHead = reduxForm({
  form: "CetakBarcodeHead",
  enableReinitialize: true,
})(CetakBarcodeHead);
const selector = formValueSelector("CetakBarcodeHead");
export default connect((state) => {
  const { pkg, nw, pkg2, nw2 } = selector(state, "pkg", "nw", "pkg2", "nw2");
  return {
    gw: parseFloat(pkg || 0) + parseFloat(nw || 0),
    gw2: parseFloat(pkg2 || 0) + parseFloat(nw2 || 0),
  };
})(CetakBarcodeHead);
